.header {
  box-shadow: 1px 0px 10px rgba(0, 0, 0, 0.1);
}

.header__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 77px;
  float: left;
}

.header__wrap_right {
  float: right;
}

.header__title {
  display: inline-block;
  margin: 0px;
  font-family: "MonsterEBold";
  font-size: 16px;
  color: #002d40;
  text-transform: uppercase;
  margin-left: 10px;
}

.header__link {
  margin-left: 18px;
  position: relative;
  cursor: pointer;
  display: inline-block;
}

.header__link-center {
  display: flex;
  align-items: center;
}

.header__link-inner {
  position: absolute;
  background: #1aa6b7;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  top: -10px;
  right: -7px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__link-text {
  font-family: "MonsterBold";
  color: #fff;
}

.header__avatar {
  background-position: center center;
  border-radius: 50%;
  width: 50px;
  background-repeat: no-repeat;
  height: 50px;
  display: inline-block;
  margin-right: 8px;
}

.header__triangle {
  display: inline-block;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 8px solid #ff414d;
}

.header__triangle-right {
  display: inline-block;
  width: 0;
  height: 0;
  border-left: 8px solid #ff414d;
  border-bottom: 4px solid transparent;
  border-top: 4px solid transparent;
}

.header__form {
  position: relative;
  width: 225px;
}

.header__form::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 100%;
  top: 9px;
  cursor: pointer;
  left: 16px;
  background: url("../img/headsearch.png") no-repeat;
}

.header__search {
  border: 1px solid #ededed;
  border-radius: 17px;
  width: 245px;
  height: 36px;
  outline: none;
  font-family: "MonsterRegular";
  color: #002d40;
  text-indent: 32px;
}

.header__search::placeholder {
  color: #002d40;
  padding-left: 3px;
  font-family: "MonsterRegular";
}

.header__box {
  width: 380px;
  position: absolute;
  top: 78px;
  left: 0;
  display: none;
  box-shadow: 1px 0px 10px rgba(0, 0, 0, 0.1);
  z-index: 5;
  background: #fff;
}

.header__box-top {
