/*CHECKBOX*/
.check {
  vertical-align: top;
  margin: 0 3px 0 0;
  width: 10px;
  height: 10px;
}

.check + label {
  cursor: pointer;
}

.check:not(checked) {
  position: absolute;
  display: none;
}

.check:not(checked) + label {
  position: relative;
  padding: 0 27px;
  margin: 0 10px;
  top: -17px;
}

.check:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 55px;
  height: 25px;
  border-radius: 10px;
  background: #CDD1DA;
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.2);
}

.check:not(checked) + label:after {
  content: '';
  position: absolute;
  top: 0;
  left: 2px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  transition: all .2s;
}

.check:checked + label:before {
  background: #002840;
}

.check:checked + label:after {
  left: 32px;
}

